html,
body {
  margin: 0;
  padding: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #000000;
  background-color: #ffffff;
  cursor: default;
}
a:hover,
a:focus,
a:active {
  color: black !important;
}
button {
  padding: 0 !important;
  border: 0 !important;
  cursor: pointer;
}
:focus {
  outline: none !important;
}
.visually-hidden {
  position: absolute;
  clip: rect(0 0 0 0);
  width: 0;
  height: 0;
  margin: 0;
  padding: 0;
}
.logo {
  text-transform: uppercase;
  transition: color 0.4s;
}
.logo:hover,
.logo:focus {
  color: #00bfff !important;
}
.logo:active {
  color: #1e90ff !important;
}
.site-navigation__toggler {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 25px;
  border: 0 !important;
  transition: color 0.4s;
  cursor: pointer;
}
.site-navigation__toggler__button {
  position: relative;
  width: 35px !important;
  height: 3px !important;
  background-color: #fff;
  border-radius: 3px;
  transition: 0.4s;
}
.site-navigation__toggler__button::before {
  content: "";
  position: absolute;
  top: -10px;
  display: block;
  width: 35px;
  height: 3px;
  background-color: #fff;
  border-radius: 3px;
  transition: 0.4s;
}
.site-navigation__toggler__button::after {
  content: "";
  position: absolute;
  bottom: -10px;
  display: block;
  width: 35px;
  height: 3px;
  background-color: #fff;
  border-radius: 3px;
  transition: 0.4s;
}
.site-navigation__toggler:hover .site-navigation__toggler__button,
.site-navigation__toggler:focus .site-navigation__toggler__button {
  background-color: #00bfff;
}
.site-navigation__toggler:hover .site-navigation__toggler__button::before,
.site-navigation__toggler:focus .site-navigation__toggler__button::before,
.site-navigation__toggler:hover .site-navigation__toggler__button::after,
.site-navigation__toggler:focus .site-navigation__toggler__button::after {
  background-color: #00bfff;
}
.site-navigation__toggler--close .site-navigation__toggler__button {
  background: none;
  transition: 0.4s;
}
.site-navigation__toggler--close .site-navigation__toggler__button::before {
  top: 0;
  transform: rotate(45deg);
}
.site-navigation__toggler--close .site-navigation__toggler__button::after {
  bottom: 0;
  transform: rotate(-45deg);
}
.site-navigation__toggler--close:hover .site-navigation__toggler__button,
.site-navigation__toggler--close:focus .site-navigation__toggler__button {
  background: none;
}
.site-navigation__toggler--close:hover .site-navigation__toggler__button::before,
.site-navigation__toggler--close:focus .site-navigation__toggler__button::before,
.site-navigation__toggler--close:hover .site-navigation__toggler__button::after,
.site-navigation__toggler--close:focus .site-navigation__toggler__button::after {
  background-color: #ff0000;
}
.page-navigation__link {
  transition: color 0.4s;
}
.page-navigation__link:hover,
.page-navigation__link:focus {
  color: rgba(0, 191, 255, 0.75) !important;
}
.page-navigation__link:active {
  color: rgba(30, 144, 255, 0.75) !important;
}
.user-navigation__link {
  transition: color 0.4s;
}
.user-navigation__link:hover,
.user-navigation__link:focus {
  color: rgba(0, 191, 255, 0.75) !important;
}
.user-navigation__link:active {
  color: rgba(30, 144, 255, 0.75) !important;
}
